    <div class="filters">
      <div class="filter form-group">
        <label>المرحلة التعليمية</label>
        <select
          @change="
            (e) => {
              this.getUnits(e);
              filterExams();
            }
          "
          class="form-control"
          v-model="filter.stage"
        >
          <option selected value="1">الصف الأول الاعدادي</option>
          <option selected value="2">الصف الثاني الاعدادي</option>
          <option selected value="3">الصف الثالث الاعدادي</option>
          <option selected value="4">الصف الأول الثانوي</option>
          <option selected value="5">الصف الثاني الثانوي</option>
          <option selected value="6">الصف الثالث الثانوي</option>
        </select>
      </div>

      <!--------------------------UNIT----------------------------->
      <div class="filter form-group">
        <label>الوحدة</label>
        <select @change="filterExams" class="form-control" v-model="filter.unit">
          <option v-for="unit in units" :key="unit.number" selected :value="unit.number">
            {{ unit.number | section(filter.stage) }}
          </option>
        </select>
      </div>
      <div class="filter form-group">
        <label>العام الدراسي</label>
        <select @change="filterExams" class="form-control" v-model="filter.year">
          <option v-for="y in years" :key="y" :value="y">{{ y }}/{{ y + 1 }}</option>
        </select>
      </div>

      <div class="filter form-group">
        <label>مفعل أم مجدول</label>
        <select @change="filterExams" class="form-control" v-model="filter.active">
