    </div>
    <div class="container card card-body">
      <div class="row">
        <div
          style="margin: 10px"
          class="col-md-3 p-0 mt-4 mx-lg-2"
          v-for="(exam, e) in exams"
          :key="e"
        >
          <exam-card
            :exam="exam"
            :editExam="editExam"
            :solveExam="solveExam"
            :deleteExam="deleteExam"
          />
        </div>
      </div>
    </div>
  </div>
