<script>
import axios from "axios";
import ExamCard from "../exam-card";
export default {
  components: {
    ExamCard,
  },

  created() {
    this.loading = true;
    this.filter.year = new Date().getFullYear();
    this.getUnits();
    const years = [];
    this.filterExams();
    for (let y = new Date().getFullYear(); y >= 2020; y--) {
      years.push(y);
    }
    this.years = years;
  },
  data() {
    return {
      exams: [],
      modal: false,
      examId: "",
      loading: false,
      years: [],
      filter: {
        stage: "6",
        year: 2021,
        number: 0,
        active: "all",
        unit: 1,
      },
      solve: false,
      currentExam: null,
      solutionModel: null,
      units: [],
    };
  },
  methods: {
    editExam(id) {
      this.$router.push("/dashboard/edit-unit-exam/" + id);
    },

    solveExam(id) {
      this.solve = false;
      this.$router.push("/dashboard/unit-exam-solutionModel/" + id);
      setTimeout(() => {
        this.solve = true;
      }, 200);
      // this.$refs.modal_button.click();
    },
    filterExams() {
      this.loading = true;
      axios.post("/filterUnitsExams", this.filter).then((res) => {
        this.exams = res.data.exams;
        this.loading = false;
      });
    },
    deleteExam(id) {
      const confirmed = confirm("هل تريد مسح الامتحان نهائيا ؟");
      if (confirmed) {
        axios
          .get("/deleteUnitExam/" + id + "/" + this.filter.year + "/" + this.filter.stage)
          .then((res) => {
            if (res.data.err)
              return this.$store.dispatch("writemessage", "لم يتم المسح حاول مرة أخرى");
            this.exams.splice(
              this.exams.findIndex((exam) => exam._id == id),
              1
            );
            this.modal = false;
          });
      }
    },
    getUnits(e) {
      const stage = e?.target?.value || "6";
      this.units = this.$store.getters.sections.filter((unit) => unit.stage == stage);
    },
  },
};
</script>

